import React, {useEffect, useState} from 'react';
import classes from "./RectangularWaveguides.module.sass";
import {Container} from "react-bootstrap";
import clsx from "classnames";
import expImg from "../img/exponential_growth_520.jpg";
import {Slider, TextField} from "@mui/material";
const marks = [
    {
        value: 0,
        label: '',
    },
    {
        value: 1,
        label: '',
    },
    {
        value: 2,
        label: '',
    },
];

function VolumetricResonator(props) {


    const [translateX, setTranslateX] = useState(0) //  
    const [sliderValue, setSliderValue] = useState(0) //  slider

    useEffect(() => {

        setTranslateX(sliderValue * 3);
    }, [sliderValue])
    return (
        <Container className={classes.container}>
            <h1>     </h1>
            <div className={classes.flexContainerItem}>
                <TextField
                    label=" ()"
                    type="number"
                    InputLabelProps={{
                        shrink: true,
                    }}
                />
            </div>
            <div className={classes.flexContainerItem}>
            <TextField
                label=""
                defaultValue="1 "
                InputProps={{
                    readOnly: true,
                }}
            />
        </div>
            <h2> :</h2>
            <div className={clsx(classes.flexContainerItem, classes.flexItemSlider)}>
                <h3> </h3>
                <div className={classes.sliderBox}>
                    <Slider
                        aria-label="Restricted values"
                        defaultValue={0}
                        // getAriaValueText={valuetext}
                        onChange={(e) => {
                            setSliderValue(e.target.value)
                        }}
                        step={null}
                        valueLabelDisplay="off"
                        marks={marks}
                        max={2}
                    />
                </div>
            </div>
        </Container>
    );
}

export default VolumetricResonator;